import React, { Component } from 'react';
import $ from 'jquery/dist/jquery';
import 'popper.js';
import 'bootstrap/dist/js/bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import 'bootstrap-icons/font/fonts/bootstrap-icons.woff';
import './categories.css'
import Topbar from '../topbar/topbar';
// import CategoryCard from '../categoryCard/categoryCard';
import firebase from '../../Firebase';
var path = require("path")

export default class Categories extends Component {
    constructor(props) {
        super(props)
        this.state = {
            category: [],
            services: [],
            show: true,
            newService: "",
            selectedCategory: '',
            updatingService: false
        }
        this.newService = this.newService.bind(this);
    }


    showServices = async (e) => {
        this.setState({ selectedCategory: e });
        let location = path.join("categories/", e, "sub_categories")
        console.log(location)
        const ref = await firebase.database().ref(location);
        let list1 = []
        await ref.once('value', (snapshot) => {
            snapshot.forEach(childSnapshot => {
                var childKey = childSnapshot.key;
                var childData = childSnapshot.val();
                console.log("the child Key is ", childKey);
                console.log("the child date is ", childData);
                console.log(childData)
                list1.push(childData);
            });
        })
        this.setState({ services: list1 });
        console.log(JSON.stringify(this.state.services))
    }

    addServices = async () => {
        if (this.state.selectedCategory !== '') {
            let file = document.getElementById("serviceIcon").files[0];
            this.setState({ updatingService: true, file: file }, async () => {
                let ref = await firebase.database().ref("sub_categories").push();
                let serviceDetails = {
                    id: ref.key,
                    name: this.state.newService,
                    logo: "https://firebasestorage.googleapis.com/v0/b/sahayi-58ad6.appspot.com/o/Sub_categories%2Flogo%2Flogo.webp?alt=media&token=70de4bc4-3cc8-427d-aa9c-fab88d8fb929"
                }
                ref.update(serviceDetails).then(() => {
                    if (this.state.file) {
                        let storageRef = firebase.storage().ref("Sub_categories");
                        storageRef.child(this.state.newService).child(this.state.newService).put(this.state.file).then((data) => {
                            alert("Service Icon added.");
                            data.ref.getDownloadURL().then(url => {
                                ref.update({
                                    logo: url
                                }).then(() => {
                                    // alert("Service Icon added");
                                    serviceDetails.logo = url;
                                    firebase.database().ref("categories/" + this.state.selectedCategory + "/sub_categories/" + ref.key).update(serviceDetails).then(() => {
                                        console.log("Service added to categories succesfully");
                                        $('#exampleModal').modal('hide');
                                        this.setState({ newService: "", updatingService: false }, () => {
                                            alert("Service added successfully");
                                        });
                                    }).catch(error => {
                                        console.log("Failed to add service : FAILED TO UPDATE CATEGORY ", error);
                                        this.setState({ updatingService: false }, () => {
                                            alert("Failed to add service.");
                                        })
                                    })
                                })
                            })
                        }).catch(error => {
                            console.log("Error occured while adding service Icon ", error);
                            this.setState({ updatingService: false }, () => {
                                alert("Failed to add Service Icon");
                            })
                        })
                    }
                    else {
                        console.log("file is none");
                        firebase.database().ref("categories/" + this.state.selectedCategory + "/sub_categories/" + ref.key).update(serviceDetails).then(() => {
                            console.log("Service added to categories succesfully");
                            $('#exampleModal').modal('hide');
                            this.setState({ newService: "", updatingService: false }, () => {
                                alert("Service added successfully");
                            });
                        }).catch(error => {
                            console.log("Failed to add service : FAILED TO UPDATE CATEGORY ", error);
                            this.setState({ newService: "", updatingService: false }, () => {
                                alert("Failed to add service.");
                            })
                        })
                    }
                }).catch(error => {
                    console.log("error happened while adding service ", error);
                    this.setState({ newService: "", updatingService: false }, () => {
                        alert("Failed to add serivce. Please try again.");
                    })
                })
            })
        } else {
            alert("First select a category from the right side then add a service.")
        }
    }

    showCategory = async () => {
        const ref = await firebase.database().ref('/categories');
        let list1 = []
        await ref.once('value', (snapshot) => {
            snapshot.forEach(childSnapshot => {
                var childKey = childSnapshot.key;
                var childData = childSnapshot.val();
                childData.key = childKey;
                list1.push(childData);
            });
        })
        console.log(list1)
        this.setState({ category: list1 });
    }


    newService(e) {
        this.setState({ newService: e.target.value })
    }

    componentDidMount() {
        this.showCategory();
    }

    render() {
        console.log(this.state.newService)
        return (
            <div className="h-100 d-flex flex-row">
                <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div class="modal-dialog">
                        {this.state.updatingService ?
                            <div class="modal-content align-items-center justify-content-center" style={{ height: "10rem" }} >
                                <div class="spinner-border text-primary" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                            </div>
                            :
                            <div class="modal-content">
                                <div class="modal-header">
                                    <h5 class="modal-title" id="exampleModalLabel">Add Services</h5>
                                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div class="modal-body">
                                    <div class="input-group mb-3">
                                        <input type="file" class="form-control" id="serviceIcon" required />
                                        <label class="input-group-text" for="serviceIcon">Upload</label>
                                    </div>
                                    <div class="mb-3">
                                        <label for="exampleFormControlInput1" class="form-label">Service Name</label>
                                        <input onChange={this.newService} type="text" name="service" id="service" className="form-control" placeholder="Service" required />
                                    </div>
                                </div>
                                <div class="modal-footer">
                                    <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                    <button type="button" class="btn btn-primary" onClick={() => this.addServices()}>Save changes</button>
                                </div>
                            </div>
                        }
                    </div>
                </div>
                <div className="d-flex flex-column w-25 h-100 p-3" id="services-sidebar">
                    <input type="text" name="search-text" id="search-text" placeholder="Search Services" className="form-control p-3 bg-light mt-3 mb-4" />
                    <div className="d-flex flex-row justify-content-between align-items-center mb-3">
                        <h6 className="h6 text-uppercase font-weight-bold">Services</h6>
                        <button type="button" className="btn" data-toggle="modal" data-target="#exampleModal">
                            <i className="bi bi-plus h3"  ></i>
                        </button>
                    </div>
                    {/* service card */}
                    {
                        this.state.services.map((val, index) => {
                            return (
                                <div key={index} className="d-flex flex-row w-100 bg-white align-items-center justify-content-between p-3 mt-1" style={{ height: '4em', borderRadius: 7 }}>
                                    <div className="rounded-circle bg-white" style={{ height: '3em', width: '3em' }}>
                                        <img src={val.logo} className="rounded-circle bg-white" alt="" style={{ height: '3em', width: '3em' }} />
                                    </div>
                                    <div className="font-weight-bold w-100 pl-3 text-capitalize" style={{ fontSize: '1em' }}>{val.name}</div>
                                </div>
                            )
                        })
                    }
                </div>
                <div className="w-100">
                    <Topbar logout={this.props.logout} />
                    <div className="w-100 d-flex flex-column p-3" id="categoriesContainerMain">
                        <div className="d-flex flex-row align-items-center justify-content-end px-5 my-3">
                            <input type="text" name="category-search" id="category-search" placeholder="Search Categories" className="form-control p-3 w-25 mx-3" />
                            <button className="btn btn-primary mr-5 px-5">Search</button>
                        </div>
                        <div className="d-flex flex-row mx-5 px-5">
                            <h3 className="h3 text-uppercase font-weight-light">Category</h3>
                        </div>
                        <div className="d-flex flex-row flex-wrap mx-5 px-5 h-100 justify-content-between" id="category-container">
                            {
                                this.state.category.map((val, index) => {
                                    console.log("THe conditin i s ", val.key === this.state.selectedCategory);
                                    console.log("this val.id ", val.key);
                                    console.log("HTis.sttate. ", this.state.selectedCategory);
                                    if (val.key === this.state.selectedCategory) {
                                        return (
                                            <div key={index} className="h-25 w-25 d-flex flex-column p-3 category-card mb-5 bg-primary" onClick={() => this.showServices(val.key)}>
                                                <h3 className="h3 text-capitalize">{val.name}</h3>
                                            </div>
                                        )
                                    } else {
                                        return (
                                            <div key={index} className="h-25 w-25 d-flex flex-column p-3 category-card mb-5" onClick={() => this.showServices(val.key)}>
                                                <h3 className="h3 text-capitalize">{val.name}</h3>
                                            </div>
                                        )
                                    }
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

