import React from 'react';
import { NavLink } from 'react-router-dom';
import 'popper.js';
import $ from 'jquery';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap';
import 'bootstrap-icons/font/fonts/bootstrap-icons.woff';
import 'bootstrap-icons/font/bootstrap-icons.css'
import './navbar.css';

export default class Navbar extends React.Component {
    componentDidMount() {
        $(function () {
            $('[data-toggle="tooltip"]').tooltip({ show: "300", hide: "300", trigger: "hover" })
        })
    }
    render() {
        return (
            <nav className="h-100 d-flex flex-column align-items-center " id="navbar" >
                <div className="logo d-flex flex-row align-items-center justify-content-center shadow shadow-lg">
                    <img src={require("./img/logo.webp").default} style={{width:"4rem",height:"4rem",objectFit:"cover"}} alt=""/>
                </div>
                <NavLink to="/"  activeClassName="navbar-item-active" className="navbar-item" data-toggle="tooltip" data-trigger="hover" data-placement="right" title="Add Freelancers" >
                    <i className="bi bi-person-plus-fill navbar-icon" ></i>
                </NavLink>
                <NavLink to="/freelancers" activeClassName="navbar-item-active"  className="navbar-item" data-toggle="tooltip" data-trigger="hover" data-placement="right" title="Freelancers" >
                    <i className="bi bi-people-fill navbar-icon" ></i>
                </NavLink>
                <NavLink to="/categories"  activeClassName="navbar-item-active" className="navbar-item" data-toggle="tooltip" data-trigger="hover" data-placement="right" title="Categories" >
                    <i className="bi bi-ui-checks navbar-icon" ></i>
                </NavLink>
            </nav>
        );
    }
}