import React, { Component } from 'react'
import 'jquery/dist/jquery'
import 'popper.js'
import 'bootstrap/dist/js/bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import 'bootstrap-icons/font/fonts/bootstrap-icons.woff';
import './freelancerCard.css'
import firebase from "../../Firebase"
var path = require('path');


export default class FreelancerCard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userActive: true,
            skillDetails: [],
            galleryImages: [],
        }
    }

    toggleActivate = () => {
        let location = path.join('/freelancers', this.props.uid, "active/")
        let ref = firebase.database().ref(location)
        if (this.state.userActive === true) {
            this.setState({ userActive: false })
            ref.set({
                active: "false"
            })
        } else {
            this.setState({ userActive: true })
            ref.set({
                active: "true"
            })
        }
    }

    getGalleryImages = async () => {
        let location = path.join("/Freelancers", this.props.freelancer.id, "gallery/");
        let ref = firebase.storage().ref(location);
        ref.listAll().then(response => {
            let galleryImages = [];
            console.log("The response for " + this.props.freelancer.id + "is ", response.items);
            response.items.forEach(item => {
                // let details = await item.getMetadata();
                // console.log("Details are ", details);
                item.getDownloadURL().then(link => {
                    console.log('Link is ', link);
                    galleryImages.push(link);
                    this.setState({ galleryImages });
                });
            })
        })
    }

    getFreelancerTags = () => {
        let tags = this.props.freelancer.tags;
        if (tags !== undefined && tags !== null) {
            let tagRef = firebase.database().ref("tags");
            let skillDetails = [];
            tags.forEach(tag => {
                tagRef.child(tag).once('value', snap => {
                    skillDetails.push(snap.val());
                    this.setState({ skillDetails });
                })
            })
        }
    }

    componentDidMount() {
        this.getGalleryImages().then(() => {
            this.getFreelancerTags()
        });
    }


    render() {
        let modalId = "userModalCenter-" + this.props.uid;
        let modalTarget = "#" + modalId;


        // let [...tags] = this.props.tags.split(',');
        // let skill = tags.map((val, index) => {
        //     return (
        //         <small key={index} className="text-white bg-primary px-3 py-2 mr-2 mb-2">{val}</small>
        //     )
        // })

        return (
            <div className="freelancer-card border d-flex flex-column justify-content-around align-items-center p-5 shadow my-3">
                <div id="status-indicator" className={this.state.userActive === true ? "user-active" : "user-inactive"}></div>
                <button className="w-100 h-100 bg-transparent border-0 btn" data-toggle="modal" data-target={modalTarget}>
                    <img src={this.props.freelancer.profilepicture || require("./img/profile.jpg").default} alt="profile" className="freelancer-photo" />
                    <h4 className="h4 text-capitalize">{this.props.name}</h4>
                    <small className="font-weight-bold text-capitalize" >{this.props.title}</small>
                </button>
                <div className="custom-control custom-switch">
                    <input type="checkbox" className="custom-control-input" id={`customSwitch${this.props.uid}`} checked={this.state.userActive ? true : false} onChange={this.toggleActivate} />
                    <label className="custom-control-label" htmlFor={`customSwitch${this.props.uid}`}></label>
                </div>

                {/* Modal Starts Here */}
                <div className="modal fade" id={modalId} tabIndex="-1" role="dialog" aria-labelledby="userCardTitle" aria-hidden="true">
                    <div className="modal-dialog modal-dialog-centered mr-0 h-100 my-0 profile-container" role="document">
                        <div className="modal-content h-100 border-0 rounded-0 bg-light">
                            <div className="modal-header profile-modal-header">
                                <h1 className="modal-title profile-title text-center text-capitalize mx-auto" id="userCardTitle">{this.props.name}</h1>
                                {/* <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                </button> */}
                            </div>

                            <div className="modal-body h-100 profile-body d-flex flex-column w-100 align-items-center px-5">
                                <img src={this.props.freelancer.profilepicture || require("./img/profile.jpg").default} alt="profilePic" className="proflie-pic my-3" />
                                <h3 className="h3 my-3">{this.props.title || "Add Bio"}</h3>
                                <h5 className="h5 my-3 text-capitalize">{this.props.freelancer.location !== undefined && this.props.location !== "" ? `${this.props.freelancer.location.text || "Add Location."}` : "Add Location."}</h5>
                                <div className="d-flex flex-row justify-content-center align-items-center w-100 my-3">
                                    {/* <i className="bi bi-instagram mr-3 h3 text-danger"></i>
                                    <i className="bi bi-twitter mr-3 h3 text-primary"></i>
                                    <i className="bi bi-github mr-3 h3 "></i> */}
                                    <i className="bi bi-whatsapp mr-3 h3 text-danger"></i>
                                    <a href={"https://wa.me/+91" + this.props.freelancer.whatsapp} target="_blank" rel="noopener noreferrer" >
                                        <svg xmlns="http://www.w3.org/2000/svg" width="1.75rem" height="1.75rem" fill="rgb(0,255,0)" class="bi bi-whatsapp mr-3" viewBox="0 0 16 16">
                                            <path d="M13.601 2.326A7.854 7.854 0 0 0 7.994 0C3.627 0 .068 3.558.064 7.926c0 1.399.366 2.76 1.057 3.965L0 16l4.204-1.102a7.933 7.933 0 0 0 3.79.965h.004c4.368 0 7.926-3.558 7.93-7.93A7.898 7.898 0 0 0 13.6 2.326zM7.994 14.521a6.573 6.573 0 0 1-3.356-.92l-.24-.144-2.494.654.666-2.433-.156-.251a6.56 6.56 0 0 1-1.007-3.505c0-3.626 2.957-6.584 6.591-6.584a6.56 6.56 0 0 1 4.66 1.931 6.557 6.557 0 0 1 1.928 4.66c-.004 3.639-2.961 6.592-6.592 6.592zm3.615-4.934c-.197-.099-1.17-.578-1.353-.646-.182-.065-.315-.099-.445.099-.133.197-.513.646-.627.775-.114.133-.232.148-.43.05-.197-.1-.836-.308-1.592-.985-.59-.525-.985-1.175-1.103-1.372-.114-.198-.011-.304.088-.403.087-.088.197-.232.296-.346.1-.114.133-.198.198-.33.065-.134.034-.248-.015-.347-.05-.099-.445-1.076-.612-1.47-.16-.389-.323-.335-.445-.34-.114-.007-.247-.007-.38-.007a.729.729 0 0 0-.529.247c-.182.198-.691.677-.691 1.654 0 .977.71 1.916.81 2.049.098.133 1.394 2.132 3.383 2.992.47.205.84.326 1.129.418.475.152.904.129 1.246.08.38-.058 1.171-.48 1.338-.943.164-.464.164-.86.114-.943-.049-.084-.182-.133-.38-.232z" />
                                        </svg>
                                    </a>
                                    <a href={"https://" + this.props.freelancer.facebook} target="_blank" rel="noopener noreferrer">
                                        <i className="bi bi-facebook mr-3 h3 text-primary"></i>
                                    </a>
                                </div>
                                <p className="p text-justify">
                                    {this.props.bio}
                                </p>
                                <h3 className="h3 align-self-start my-3">Skills</h3>
                                <div className="d-flex flex-row flex-wrap w-100">
                                    {
                                        this.state.skillDetails.length === 0 ?
                                            <h5 className="h5">Add skill</h5>
                                            : this.state.skillDetails.map((val, index) => {
                                                return (
                                                    <small className="text-white bg-primary px-3 py-2 mr-2 mb-2" key={val.id} >{val.tag_name}</small>
                                                )
                                            })
                                    }
                                    {/* <small className="text-white bg-primary px-3 py-2 mr-2 mb-2">{this.props.tags}</small> */}
                                    {/* {skill} */}
                                </div>
                                <h3 className="h3 align-self-start my-3">Gallery</h3>
                                <div className="d-flex flex-row flex-wrap w-100">
                                    {
                                        this.state.galleryImages.length === 0 ?
                                            <div className="d-flex flex-row align-items-center justify-content-center w-100">
                                                <h5 className="h5 text-center">No Images</h5>
                                            </div>
                                            :
                                            this.state.galleryImages.map((value, index) => {
                                                return (
                                                    <img src={value} key={value} className="gallery-image" alt="Gallery" />
                                                )
                                            })
                                    }
                                </div>
                            </div>
                            {/* <div className="modal-footer profile-footer">
                                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                            </div> */}
                        </div>
                    </div>
                </div>
                {/* Modal Ends Here */}
            </div>
        )
    }
}

