import React, { Component } from 'react';
import firebase from "../../Firebase"
import 'bootstrap-icons/font/fonts/bootstrap-icons.woff';
import 'bootstrap-icons/font/bootstrap-icons.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import './login.css';

export default class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: "",
            password: "",
        }
    }

    signInUser = () => {
        if (this.state.username === "admin@gmail.com") {
            firebase.auth().signInWithEmailAndPassword(this.state.username, this.state.password).then(user => {
                if (user) {
                    this.props.authenticate(user);
                }
            }).catch(error => {
                console.log("Error while loggin in ", error);
                alert("Login Failed. Try again");
            })
        }
    }

    handleUsername = (e) => {
        this.setState({ username: e.target.value });
    }

    handlePassword = (e) => {
        this.setState({ password: e.target.value });
    }

    render() {
        return (
            <div className="w-100 h-100 d-flex flex-row" >
                <div id="splash">
                    <img src={require("./img/loginSplash.jpg").default} className="splash-image" alt="Login Splash" />
                </div>
                <div id="loginContainer" className=" d-flex flex-column justify-content-around">
                    <div className="d-flex justify-content-center align-items-center w-100 flex-row py-5">
                        {/* <i className="bi bi-github h3 mx-3"></i> */}
                        <img src={require("./img/logo.webp").default} className="h3 mx-3" style={{ height: "5rem", width: '5rem' }} alt="" />
                        <h3 className="h3">Login</h3>
                    </div>
                    <div className="container h-75 px-5">
                        <div className="d-flex flex-column w-100 align-items-center justify-content-center p-5">
                            <h3 className="h3 text-center text-capitalize">Welcome back,</h3>
                            <h3 className="h3 text-center text-capitalize">Admin</h3>
                            <div className="form-group w-100 mx-5 mt-5 mb-3 w-100">
                                <input type="email" name="email" id="email" placeholder="youremail@gmail.com" className="form-control w-100 p-3" value={this.state.username} onChange={this.handleUsername} required />
                            </div>
                            <div className="form-group mx-5 w-100 mb-3">
                                <input type="password" name="password" id="password" placeholder="Password" className="form-control w-100 p-3" value={this.state.password} onChange={this.handlePassword} required />
                            </div>
                            <div className="d-flex flex-row justify-content-end align-items-center w-100 px-5 mb-3">
                                <a href="#email" className="text-muted text-capitalize">Forgot Password</a>
                            </div>
                            <button type="button" className="btn btn-primary text-capitalize w-100 mx-5 p-3" onClick={() => { this.signInUser() }} >Login</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

