import React from 'react';
import './topbar.css';
import 'bootstrap-icons/font/fonts/bootstrap-icons.woff';
import "bootstrap-icons/font/bootstrap-icons.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle';
import firebase from '../../Firebase'

export default class Topbar extends React.Component {
    logoutUser = () => {
        firebase.auth().signOut().then(() => {
            localStorage.clear();
            sessionStorage.clear();
            this.props.logout();
        }).catch(error => {
            console.log("Failed to logout ", error);
            alert("Logout Failed");
        })
    }
    render() {
        return (
            <div className="w-100 bg-light d-flex flex-row align-items-center" id="topbar-container">
                <div className="d-flex flex-row align-items-center ml-auto mr-5">
                    <img src={require("./img/logo.png").default} className=" ml-5 mr-2" alt="profile" id="topbar-profile-pic" />
                    <h3 className="h3 mx-3">Admin</h3>
                    <div className="dropdown">
                        <button className="btn dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        </button>
                        <div className="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                            {/* <a className="dropdown-item" href="#"><i className="bi bi-gear mr-3"></i> Settings</a> */}
                            <button className="dropdown-item">
                                <i className="bi bi-gear mr-3"></i> Settings
                            </button>
                            <div className="dropdown-divider"></div>
                            <button className="dropdown-item" onClick={this.logoutUser}>
                                <i className="bi bi-arrow-right mr-3"></i> Logout
                            </button>
                            {/* <a className="dropdown-item" href="#"><i className="bi bi-arrow-right mr-3"></i> Logout</a> */}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}