import React from 'react';
import { Switch, BrowserRouter as Router, Route } from 'react-router-dom'
import './App.css';
import Navbar from './components/navbar/navbar';
import Login from './components/login/login'
import AddFreelancer from './components/addFreelancer/addFreelancer';
import Freelancers from './components/freelancers/freelancers';
import Categories from './components/categories/categories';
// import firebase from './Firebase';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isAthenticated: false
    }
  }

  authenticateUser = (user = null) => {
    this.setState({ isAthenticated: true, user: user });
  }

  logoutUser = () => {
    this.setState({ isAthenticated: false, user: null })
  }

  render() {
    return (
      <div className="App d-flex flex-row " >
        {this.state.isAthenticated ?
          <Router basename="/dashboard">
            <Navbar />
            <div className="d-flex flex-column w-100 mx-0 px-0">
              <Switch>
                <Route exact path="/" >
                  <AddFreelancer logout={this.logoutUser} />
                </Route>
                <Route path="/freelancers">
                  <Freelancers logout={this.logoutUser} />
                </Route>
                <Route path="/categories">
                  <Categories logout={this.logoutUser} />
                </Route>
              </Switch>
            </div>
          </Router>
          :
          <Login authenticate={this.authenticateUser} />
        }
      </div>
    );
  }
}
export default App;