import React, { Component } from 'react'
import './addFreelancer.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Topbar from '../topbar/topbar';
import './addFreelancer.css'
import firebase from "../../Firebase";
var path = require('path');

export default class AddFreelancer extends Component {
    constructor() {
        super();
        this.state = {
            password: "",
            fName: "",
            lName: "",
            email: "",
            sub_category: "",
            sub_categories: [],
        };
        this.submitfName = this.submitfName.bind(this);
        this.submitlName = this.submitlName.bind(this);
        this.submitPassword = this.submitPassword.bind(this);
        this.submitLogin = this.submitLogin.bind(this);
        this.submitEmail = this.submitEmail.bind(this);
    }
    submitfName(e) {
        this.setState({ fName: e.target.value });
    }
    submitlName(e) {
        this.setState({ lName: e.target.value });
    }
    submitEmail(e) {
        this.setState({ email: e.target.value });
    }
    submitPassword() {
        let pass = this.state.fName + this.state.lName + Math.floor((Math.random() * 10) + 1);
        this.setState({ password: pass })
    }
    async submitLogin() {
        if (this.state.lName && this.state.fName !== "" && this.state.sub_category !== "") {
            await this.submitPassword();
            await firebase.auth().createUserWithEmailAndPassword(this.state.email, this.state.password)
                .then(async (data) => {
                    // Signed in
                    //   console.log(JSON.stringify(data))
                    let location = path.join('/freelancers');
                    let ref = await firebase.database().ref(location).child(data.user.uid);
                    let active = await firebase.database().ref(location).child(data.user.uid).child('active');

                    await ref.set({
                        id: data.user.uid,
                        email: this.state.email,
                        name: this.state.fName + " " + this.state.lName,
                        age: "",
                        bio: "",
                        facebook: "",
                        galleryimages: "",
                        instagram: "",
                        linkedin: "",
                        whatsapp: "",
                        profilepicture: "",
                        sex: "",
                        sub_category: this.state.sub_category,
                        title: "",
                    });
                    await active.set({
                        active: "true",
                    })
                    // ...
                    alert('Account Created.');
                    this.setState({
                        password: "",
                        fName: "",
                        lName: "",
                        email: "",
                        sub_category: "",
                    })
                })
                .catch((error) => {
                    //   var errorCode = error.code;
                    var errorMessage = error.message;
                    console.log(errorMessage)
                    // ..
                });
        }
        else {
            console.log("handle no lname and fname")
            alert("Please Enter Name and Select a service.");
        }
    }

    getService = () => {
        firebase.database().ref("sub_categories").once("value", snap => {
            let sub_categories = [];
            for (let i in snap.val()) {
                sub_categories.push(snap.val()[i])
            }
            this.setState({ sub_categories });
        })
    }

    handleService = (event) => {
        this.setState({ sub_category: event.target.value })
    }

    componentDidMount() {
        this.getService();
    }



    render() {
        return (
            <div className="w-100 h-100">
                <Topbar logout={this.props.logout} />
                <div className="container-fluid p-5 w-100">

                    <div className="d-flex flex-column mx-5 px-5 justify-content-around border py-3" id="freelancer-form">
                        <h1 className="h1 text-capitalize mx-5 px-5 w-50 align-self-center">Add Freelancer</h1>
                        <div className="d-flex flex-row mx-5 px-5 w-50 align-self-center">
                            <input onChange={this.submitfName} value={this.state.fName} type="text" name="fname" id="fname" className="form-control mr-5" placeholder="First Name" required />
                            <input onChange={this.submitlName} value={this.state.lName} type="text" name="lname" id="lname" className="form-control ml-5" placeholder="Last Name" required />
                        </div>
                        <div className="mx-5 px-5 w-50 align-self-center">
                            <input onChange={this.submitEmail} value={this.state.email} type="email" name="email" id="email" className="form-control" placeholder="emailid@example.com" required />
                        </div>
                        <div className="mx-5 px-5 w-50 align-self-center">
                            <select name="category" id="category" className="custom-control custom-select" placeholder="Category" value={this.state.sub_category} onChange={this.handleService}>
                                <option defaultChecked value="">Select Service</option>
                                {
                                    this.state.sub_categories.map((value, index) => {
                                        console.log("Value inside the select is ", value);
                                        return (
                                            <option value={value.id}>{value.name}</option>
                                        )
                                    })
                                }
                            </select>
                        </div>
                        <div className="mx-5 px-5 w-50 align-self-center">
                            <button onClick={this.submitLogin} className="btn btn-primary text-capitalize w-100">Generate Freelancer Account</button>
                            {/* <button onClick={this.submitPassword} className="btn btn-primary text-capitalize w-100">pass</button> */}

                        </div>
                    </div>

                    <div className="d-flex flex-column m-5 p-5 bg-light">
                        <div className="mx-5 px-5">
                            <div className="d-flex flex row ml-2">
                                <h3 className="h3 w-50 text-primary">Email</h3>
                                <h3 className="h3 text-primary">{this.state.email}</h3>
                            </div>
                            <div className="d-flex flex row ml-2">
                                <h3 className="h3 w-50 text-primary">Password</h3>
                                <h3 className="h3 text-primary">{this.state.password}</h3>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

